.book {
  height: 50rem;
  background: linear-gradient(114deg, rgba(255,255,255,0.8) 0%, rgba(255,255,255,0.8) 57%, rgba(186,133,84,0.8) 57%, rgba(161,79,3,0.8) 100%), url(../images/bg-form.jpg) center / cover no-repeat;
  box-shadow: $box-shadow;
  border-radius: .5rem;
  overflow: hidden;
  @include respond(tab-port) {
    background: linear-gradient(114deg, rgba(255,255,255,0.8) 0%, rgba(255,255,255,0.8) 507%, rgba(186,133,84,0.8) 57%, rgba(161,79,3,0.8) 100%), url(../images/bg-form.jpg) center / cover no-repeat; } }
.form {
  width: 70%;
  padding-top: 5rem;
  padding-left: 50px;
  &__group:not(:last-child) {
    margin-bottom: 2rem; }
  &__group:nth-child(4) {
    margin-bottom: 4rem; }
  @include respond(tab-land) {
    &__group:nth-child(4) {
      margin-bottom: 2rem; }
    &__group:not(:last-child) {
      margin-bottom: 0; } }
  @include respond(tab-port) {
    width: 100%;
    padding-top: 3rem;
    padding-left: 3rem; }
  &__input {
    color: $color-black;
    padding: 2rem 0rem 2rem 2rem;
    background: $color-white;
    border: none;
    border-bottom: .3rem solid transparent;
    border-radius: .5rem;
    width: 70%;
    @include respond(tab-land) {
      width: 90%;
      box-shadow: $box-shadow; }
    &:focus {
      outline: none;
      border-bottom: .3rem solid #66D763; }
    &:focus:invalid {
      border-bottom: .3rem solid #D76363; } }
  &__input:placeholder-shown + &__label {
    opacity: 0;
    visibility: hidden;
    transform: translateY(-3rem); }
  &__label {
    font-size: $font-size-paragraph;
    letter-spacing: .2rem;
    font-weight: 400;
    display: block;
    margin-top: 1rem;
    margin-left: 1.5rem;
    transition: .2s all; }
  &__group-radio {
    display: inline-block;
    &:not(:last-child) {
      margin-right: 2rem; }
    @include respond(tab-land) {
      display: block;
      margin-bottom: 2rem; } }
  &__input-radio {
    display: none; }
  &__input-radio:checked ~ &__label-radio &__span-radio::after {
    opacity: 1; }
  &__label-radio {
    cursor: pointer;
    position: relative;
    padding-left: 4rem; }

  &__span-radio {
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
    border: .5rem solid $color-dark-brown;
    display: block;
    position: absolute;
    left: 0;
    top: -.4rem;
    &::after {
      content: "";
      display: block;
      height: 1.5rem;
      width: 1.5rem;
      border-radius: 50%;
      background: $color-dark-brown;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      opacity: 0;
      transition: .2s all; } } }
